<template>
  <div id="PhotoMemory" class="pb-16">
    
    <div v-show="alert.status" :class="alert.class" class="fixed z-50 w-full flex justify-center alertFade" style="top:40%">
      <span :class="alert.color" class="px-10 py-5 rounded-lg font-bold text-black text-3xl" v-text="alert.msg" />
    </div>
    <div class="w-full flex flex-col items-center mt-16 mb-5 font-bold text-sm lg:text-xl">
      <div class="w-full flex flex-wrap justify-center max-w-5xl items-center">
        <div class="">
          <button class='bg-purple-500 uppercase rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="changeSettings()">{{ $t("photo-memory.restart") }}</button>
        </div>
      </div>
      <div class="w-full flex flex-wrap justify-between max-w-5xl mt-5 items-center">
        <div class="ml-10">{{ $t("photo-memory.count") }} : <span :class="getColor(1)" v-text="count +'/'+ photos.length/2" /></div>
        <div class="mr-10">{{ $t("photo-memory.timeLeft") }} : <span :class="getColor(2)" v-text="timer"/> {{ $t("photo-memory.timeLeftS") }}</div>
      </div>
    </div>
    <div class="w-full flex justify-center">
      <div class="w-full flex flex-wrap max-w-5xl">
        <div class="md:p-1 w-1/6 h-20 sm:h-24 md:h-32" v-for="(item, index) in photos" :key="index">

          <div class="flip-box rounded-lg overflow-hidden cursor-pointer " @click="addfilp(item)">
            <div :class="item.isFilp ? 'flip-now':''" class="flip-box-inner">
              <div class="flip-box-front">
                <img class="w-full h-full object-center" src="../assets/img/logo.png" alt="musab">
              </div>
              <div class="flip-box-back">
                <img class="w-full h-full object-center" :src="item.imgPath" alt="musab">
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>

    <LevelsSelect @dataForApp="updateLevelValue" />
  
  </div>
</template>

<script>
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'PhotoMemory',
  components: {
    LevelsSelect
  },
  data() {
    return {
      hasfilp:false,
      first:null,
      scound:null,

      count:0,

      timeLimit: 0,
      timePassed: 0,
      timerInterval: null,

      stopGame:false,

      level:0,

      alert:{status:false,class:"opacity-0",color:null,msg:null},

      photos:[]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      //this.addImg();
      this.randomAddImg();

      this.viewAllImg();
      this.addTimeLimit();
    },
    addfilp(obj){
      if(this.first != null && this.scound != null)return;
      if(obj.isFilp == true)return;
      if(this.stopGame)return;
      if(!this.timerInterval) this.startTimer();
      obj.isFilp = true
      if (!this.hasfilp) {
        this.first = obj
        this.hasfilp = true;
      }else{
        this.scound = obj
        this.hasfilp = false;
        this.check();
      }
    },
    check(){
      var vm = this;
      if (vm.first.id == vm.scound.id) {
        console.log(vm.first.id +":"+ vm.scound.id)
        vm.count++;
        this.ifAllDone();
        vm.first = null;
        vm.scound = null;
      }else{
        setTimeout(function() {
          vm.first.isFilp = false;
          vm.scound.isFilp = false;
          vm.first = null;
          vm.scound = null;
        }, 1000);
      }
    },
    ifAllDone(){
      var done = true;
      this.photos.forEach(item => {
        if(item.isFilp != true){
          done = false;
          return false;
        }
      });
      if(done) {
        this.alertShow("You Win :)");
        this.stopTimer();
        console.log("allFilpDone");
      }
    },
    addImg(){
      var n;
      if(this.level == 0)n=18;
      else if(this.level == 1)n=24;
      else if(this.level == 2)n=30;
      for (let i = 0; i < n; i++) {
        this.photos.push({
          id: Math.floor(i/2),
          imgPath:require("../assets/img/"+Math.floor(i/2)+".jpg"),
          isFilp:false
        });
      }
    },
    randomAddImg(){
      var n;
      if(this.level == 0)n=18;
      else if(this.level == 1)n=24;
      else if(this.level == 2)n=30;
      for(var a=[],i=0;i<n;i++)a[i]=i;

      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      
      for (let n = 0; n < a.length; n++) {
        this.photos.push({
          id: Math.floor(a[n]/2),
          imgPath:require("../assets/img/"+Math.floor(a[n]/2)+".jpg"),
          isFilp:false
        });
      }
    },
    addTimeLimit(){
      var time = 0;
      if(this.level == 0)time=60;
      else if(this.level == 1)time=80;
      else if(this.level == 2)time=100;

      this.timeLimit = time;
    },
    viewAllImg(){
      var time = 0;
      if(this.level == 0)time=3000;
      else if(this.level == 1)time=4000;
      else if(this.level == 2)time=5000;
      var vm=this;
      vm.photos.forEach(item => {
        item.isFilp = true;
      });
      clearTimeout(this.viewAllImgTimer)
      this.viewAllImgTimer = setTimeout(function() {
        vm.photos.forEach(item => {
          item.isFilp = false;
        });
      }, time);
    },

    getColor(n){
      var vm = this;
      if(n == 1){
        if(vm.count == (vm.photos.length/2))return 'text-green-500'
        else if(vm.count >= (vm.photos.length/2)/2)return 'text-yellow-500'
        else return 'text-red-500'
        }
      if(n == 2){
        if(vm.timePassed == vm.timeLimit)return 'text-red-500'
        else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
        else return 'text-green-500'
      }
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(){
      clearInterval(this.timerInterval);
    },
    gameOver(){
      if(this.stopGame == false){
        this.stopGame = true;
        this.stopTimer()
        if(this.first)this.first.isFilp = false;
        this.alertShow("Game Over :(","bg-red-500");
        console.log("Game Over")
      }
    },

    alertShow(msg,color = "bg-green-500"){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = color;
      vm.alert.msg = msg;

      vm.alertFadeOut(8000);
    },
    alertFadeOut(sec){
      var vm = this;
      setTimeout(function(){
        vm.alert.class = "opacity-0"
        setTimeout(function() {
          vm.alert.status = false;
        }, 500);
      },sec);
    },

    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.stopTimer();
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.gameOver();
      return timeLeft
    }
  },
}
</script>

<style>
.flip-box{
  background-color: transparent;
  width: 100%;
  height: 100%;
  border: 2px solid #f1f1f1;
  perspective: 1000px;
}

.flip-box-inner{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 1.5s;
  transform-style: preserve-3d;
}

.flip-now{
  transform: rotateY(180deg);
}

.flip-box-front, .flip-box-back{
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-box-front{
  background-color: #bbb;
  color: black;
}

.flip-box-back{
  background-color: #555;
  color: white;
  transform: rotateY(180deg);
}

.alertFade{
  -webkit-transition: opacity 3s ease-in-out;
  -moz-transition: opacity 3s ease-in-out;
  -ms-transition: opacity 3s ease-in-out;
  -o-transition: opacity 3s ease-in-out;
}
</style>
